.accordion__title {
  position: relative;
  font-size: 1rem;
  padding: 1rem;
  cursor: pointer;
  color: #fff;
  background-color: #408f95;
}
@media screen and (min-width: 768px) {
  .accordion__title {
    font-size: 1.25rem;
    padding: 1.5rem 2.5rem;
  }
}

.accordion__title::before,
.accordion__title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1rem;
  width: 1.75rem;
  height: 0.0625rem;
  background-color: #fff;
  transition: 0.3s transform, 0.3s opacity;
}
@media screen and (min-width: 768px) {
  .accordion__title::before,
  .accordion__title::after {
    right: 2.5rem;
  }
}

.accordion__title::before {
  transform: rotate(180deg);
}

.accordion__title::after {
  opacity: 0;
}

.accordion__title.is-closed::before {
  transform: rotate(90deg);
}

.accordion__title.is-closed::after {
  opacity: 1;
}

.accordion__content {
  display: block;
  padding: 1.3rem 1rem 1.2rem;
  border-right: 0.0625rem solid #408f95;
  border-bottom: 0.0625rem solid #408f95;
  border-left: 0.0625rem solid #408f95;
}
