.button {
  width: 12.625rem;
  height: 3.375rem;
  position: relative;
  display: inline-block;
  font-family: "Gotu", sans-serif;
  font-weight: normal;
  font-size: 0.875rem;
  color: #fff;
  background-color: #408f95;
  border: 0.0625rem solid #408f95;
  padding: 1rem 0;
  text-align: center;
  transition: 0.3s background-color;
  z-index: 20;
}

.button--white {
  color: #408f95;
  border: 0.0625rem solid #fff;
  background-color: #fff;
}

.button__span,
.button:hover .button__span {
  display: inline-block;
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 38%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

.not-found__button-wrapper .button__span,
.not-found__button-wrapper .button:hover .button__span {
  left: 35%;
}

.button:hover .button__span {
  color: #408f95;
}

.button--white:hover .button__span {
  color: #fff;
}

.button::after {
  width: 12.625rem;
  height: 3.375rem;
  display: inline-block;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  content: "";
  border: 0.0625rem solid #408f95;
}

.button--white::after {
  border: 0.0625rem solid #fff;
}

.button::before {
  background-color: #fff;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
}

.button--white::before {
  background-color: #408f95;
}

.button:hover::before {
  display: inline-block;
  transform: scale(1, 1);
}

.button__span::after,
.button:hover .button__span::after {
  width: 2.5rem;
  height: 0.375rem;
  margin-left: 0.5rem;
  position: absolute;
  top: 30%;
  display: inline-block;
  content: "";
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.button__span::after {
  background-image: url(../images/common/arrow-btn_white.svg);
  margin-left: 1rem;
}

.button--white .button__span::after {
  background-image: url(../images/common/arrow-btn_blue.svg);
  margin-left: 1rem;
}

.button:hover .button__span::after {
  background-image: url(../images/common/arrow-btn_blue.svg);
  z-index: 20;
}

.button--white:hover .button__span::after {
  background-image: url(../images/common/arrow-btn_white.svg);
  z-index: 20;
}

.button__span--uppercase {
  display: inline-block;
  text-transform: uppercase;
}

