}

.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  position: relative;
  display: inline-flex;
  width: 2.375rem;
  height: 2.375rem;
  margin: 0;
}

.wp-pagenavi .previouspostslink {
  margin-right: 2.1rem;
}

.wp-pagenavi .nextpostslink {
  margin-left: 2.1rem;
}

