  margin-right: 0;
}

.sub-blog__pagenavi--single .wp-pagenavi .nextpostslink {
  margin-left: 0;
}

.wp-pagenavi .previouspostslink::before,
.wp-pagenavi .nextpostslink::before {
  display: block;
  position: absolute; /*相対配置*/
  content: "";
  width: 0.59375rem; /*線の横の長さ*/
  height: 0.59375rem; /*線の縦の長さ*/
  color: #408f95; /*線の色*/
}

.previouspostslink:hover::before {
  color: #fff;
}

.nextpostslink:hover::before {
  color: #fff;
}

.wp-pagenavi .previouspostslink::before {
  border-top: solid 0.0625rem; /*横の線の太さ*/
  border-left: solid 0.0625rem; /*縦の線の太さ*/
  transform: rotate(-45deg);
  top: 35%;
  right: 32%;
}

.wp-pagenavi .nextpostslink::before {
  border-top: solid 0.0625rem; /*横の線の太さ*/
  border-right: solid 0.0625rem; /*縦の線の太さ*/
  transform: rotate(45deg);
  top: 35%;
  left: 32%;
}

.wp-pagenavi a,
.wp-pagenavi span {
  margin: 0 0.375rem;
  display: inline-flex;
  width: 2.375rem;
  height: 2.375rem;
  padding-top: 0.5rem;
  padding-inline: 0.875rem;
  font-size: 1rem;
  line-height: 1;
  border: 1px solid #408f95;
  background-color: #fff;
  transition: 0.3s;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
  color: #408f95;
}

.wp-pagenavi a:hover {
  color: #fff;
  background-color: #408f95;
  opacity: 1;
}

