@use "global" as *;

.top-about {
}

.top-about__container {
  margin-top: rem(32);
  @include mq("md") {
    margin-top: rem(40);
    position: relative;
  }
}

.top-about__img-wrapper {
  position: relative;
  aspect-ratio: 345 / 212;
  width: 100%;
  height: 100%;
  @include mq("md") {
    aspect-ratio: 1080 / 661;
  }
}
.top-about__img-wrapper::before {
  @include mq("md") {
    content: "";
    background-image: url(../images/common/sango.svg);
    background-repeat: no-repeat;
    background-size: contain;
    width: rem(194);
    height: rem(181);
    position: absolute;
    bottom: rem(-94);
    right: calc(50% - min(48.5vw, rem(640)));
    z-index: 1;
  }
}

.top-about__img01 {
  width: 37.1%;
  position: relative;
  z-index: 1;
}

.top-about__img01 img,
.top-about__img02 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-about__img01 img {
  aspect-ratio: 128 / 194;
  @include mq("md") {
    aspect-ratio: 400 / 606;
  }
}

.top-about__img02 {
  width: 81.45%;
  position: absolute;
  bottom: 0;
  right: 0;
}

.top-about__img02 img {
  aspect-ratio: 281 / 186;
  @include mq("md") {
    aspect-ratio: 880 / 581;
  }
}

.top-about__body {
  margin-top: rem(40);
  @include mq("md") {
    position: absolute;
    z-index: 10;
    top: 49%;
    left: 57.4%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    margin-top: 0;
    max-width: rem(840);
    width: 100%;
    display: flex;
    gap: rem(40);
  }
}

.top-about__heading-wrapper {
  @include mq("md") {
    width: 50%;
  }
}

.top-about__heading {
  font-family: $english-font-family02;
  font-size: rem(40);
  line-height: 1;
  color: $main-blue;
  @include mq("md") {
    font-size: rem(80);
    color: $white;
    line-height: calc(10 / 8);
  }
}

.top-about__content-wrapper {
  margin-top: rem(16);
  @include mq("md") {
    width: 50%;
    margin-top: 0;
  }
}

.top-about__text {
  font-size: rem(16);
  line-height: calc(24 / 16);
  color: $black;
  font-weight: $regular;
  @include mq("md") {
    color: $white;
  }
}

.top-about__button-wrapper {
  margin-right: rem(17.6);
  margin-top: rem(24);
  text-align: center;
  @include mq("md") {
    margin-top: rem(38);
    margin-right: rem(4);
  }
}
