@use "global" as *;

.blog-card {
  width: 100%;
  background-color: $white;
  box-shadow: rem(2) rem(2) rem(10) 0 rgba(0, 0, 0, 0.25);
  margin-inline: auto;
  @include mq("md") {
    margin-inline: initial;
  }
}

.blog-card__link {
  display: block;
}

.blog-card__link:hover {
  opacity: 0.8;
}

.blog-card__inner {
  padding: rem(16) rem(16) rem(24);
  display: flex;
  flex-direction: column;
  align-items: center;
  @include mq("md") {
    padding-bottom: rem(25.6);
  }
}

.blog-card__img figure img {
  aspect-ratio: 301 / 201;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.blog-card__img--scale-bigger {
  overflow: hidden;
}

.blog-card__img--scale-bigger:hover figure img {
  transform: scale(1.2, 1.2);
  transition: 1s all;
}

.blog-card__content {
  margin-top: rem(20);
  @include mq("md") {
    margin-top: rem(16);
  }
}

.blog-card__content--sub-page {
  margin-top: rem(16);
}

.blog-card__head {
  border-bottom: rem(1) solid $main-blue;
  padding-bottom: rem(2);
  width: 100%;
}

.blog-card__date {
  font-family: $english-font-family02;
  font-size: rem(12);
}

.blog-card__title {
  font-size: rem(16);
  font-weight: $medium;
}

.blog-card__text {
  margin-top: rem(16);
  font-size: rem(16);
  line-height: calc(24 / 16);
}
